    "title": "PostgreSQL LXC",
    "content": `<p align="center"><img src="https://raw.githubusercontent.com/loganmarchione/homelab-svg-assets/main/assets/postgresql.svg" height="100"/></p>
  <h1 align="center" id="heading">PostgreSQL LXC</h1>
  <h3 align="center"> Option to Install Adminer</h3>
  
  [PostgreSQL](https://www.postgresql.org/) (often referred to as Postgres) is an open-source relational database management system that is known for its extensibility and strict adherence to SQL standards. It is a free and powerful database solution, suitable for a wide range of applications, from small projects to large enterprise systems. PostgreSQL is widely used for its reliability, feature-richness, and robustness.
  
  To create a new Proxmox VE PostgreSQL LXC, run the command below in the <b>Proxmox VE Shell</b>.
    
  ~~~bash
  bash -c "$(wget -qLO - https://github.com/tteck/Proxmox/raw/main/ct/postgresql.sh)"
  ~~~
  
  <h3 align="center" id="heading"> Default Settings:  1GB RAM - 4GB Storage - 1vCPU </h3>
  
  To make sure our PostgreSQL is secured with a strong password, set a password for its system user and then change the default database admin user account
  
  Change user password
  ~~~bash
  passwd postgres
  ~~~
  Login using Postgres system account
    
  ~~~bash
  su - postgres
  ~~~
  Now, change the Admin database password 
  ~~~bash
  psql -c "ALTER USER postgres WITH PASSWORD 'your-password';"
  ~~~
  Create a new user.
  ~~~bash
  psql
  ~~~
  ~~~sql
  CREATE USER admin WITH PASSWORD 'your-password';
  ~~~
  Create a new database:
  ~~~sql
  CREATE DATABASE homeassistant;
  ~~~
  Grant all rights or privileges on created database to the user
  ~~~sql
  GRANT ALL ON DATABASE homeassistant TO admin;
  ~~~
  To exit psql
  ~~~bash
  \\q
  ~~~ 
  Then type exit to get back to root
  
  Change the recorder: <code class="highlighter-rouge">db_url:</code> in your HA configuration.yaml
    
  Example:
  ~~~bash
  recorder:
    db_url: postgresql://admin:your-password@192.168.100.20:5432/homeassistant?client_encoding=utf8
  ~~~

   [**Adminer**](https://www.adminer.org/) is a full-featured database management tool
    
  **Adminer Interface: IP/adminer/**`
