    "title": "InfluxDB LXC",
    "content": `<p align="center"><img src="https://raw.githubusercontent.com/loganmarchione/homelab-svg-assets/main/assets/influx.svg" height="100"/></p>
  <h1 align="center" id="heading">InfluxDB LXC</h1>
  <h3 align="center"> Options to Install InfluxDB v1 or v2 and Telegraf</h3>
  
  [InfluxDB](https://www.influxdata.com/) is designed to handle high write and query loads, and is optimized for storing and analyzing time-stamped data, such as metrics, events, and logs. InfluxDB supports SQL-like query language and has a built-in HTTP API for data ingestion and retrieval. It's commonly used for IoT and industrial applications where time-series data is involved.
  
  [Telegraf](https://www.influxdata.com/time-series-platform/telegraf/) is a server agent that collects, processes, and aggregates metrics and events data from different sources, such as systems, databases, and APIs, and outputs the data to various outputs, such as InfluxDB, Prometheus, Elasticsearch, and many others.
  
  To create a new Proxmox VE InfluxDB LXC, run the command below in the <b>Proxmox VE Shell</b>.
  
  ~~~bash
    bash -c "$(wget -qLO - https://github.com/tteck/Proxmox/raw/main/ct/influxdb.sh)"
  ~~~
  
  <h3 align="center" id="heading"> Default Settings:  2GB RAM - 8GB Storage - 2vCPU </h3>
  
 InfluxDB v1.x does not have a web UI included with the software by default. Instead, it provides a command-line interface (CLI) that allows you to interact with the database and perform administrative tasks.

 If you prefer a web UI for InfluxDB, it may be worth considering upgrading to InfluxDB v2.x, which includes a built-in web UI. This interface provides a range of features for working with the database, including data visualization, querying, and management tools, all accessible via a web browser.
 
 InfluxDB V2 Interface: IP:8086

  **InfluxDB Configuration**
  
  ~~~yaml
  nano /etc/influxdb/influxdb.conf
  ~~~
  
  **Telegraf Configuration**
  
  ~~~yaml
  nano /etc/telegraf/telegraf.conf
  ~~~`
  
