    "content": `<p align="center"><img src="https://github.com/Koenkk/zigbee2mqtt/blob/master/images/logo_bee_only.png?raw=true" height="100"/></p>
  <h1 align="center" id="heading">Zigbee2MQTT LXC</h1>

  <h3 align="center"> Option to switch to Edge/dev branch</h3>
  
  [Zigbee2MQTT](https://www.zigbee2mqtt.io/) is an open-source software project that allows you to use Zigbee-based smart home devices (such as those sold under the Philips Hue and Ikea Tradfri brands) with MQTT-based home automation systems, like Home Assistant, Node-RED, and others. The software acts as a bridge between your Zigbee devices and MQTT, allowing you to control and monitor these devices from your home automation system.
  
   *If the LXC is created Privileged, the script will automatically set up USB passthrough.*

  To create a new Proxmox VE Zigbee2MQTT LXC, run the command below in the <b>Proxmox VE Shell</b>. <br>
  To Update Zigbee2MQTT, run the command below in the <b>LXC Console</b>.
  
  ~~~bash
  bash -c "$(wget -qLO - https://github.com/tteck/Proxmox/raw/main/ct/zigbee2mqtt.sh)"
  ~~~
  <h3 align="center" id="heading"> Default Settings:  1GB RAM - 4GB Storage - 2vCPU </h3>
 
  As an alternative option, you can use Alpine Linux and the Zigbee2MQTT package to create a Zigbee2MQTT LXC container with faster creation time and minimal system resource usage. <br>

  To create a new Proxmox VE Alpine-Zigbee2MQTT LXC, run the command below in the <b>Proxmox VE Shell</b>. <br>
  ~~~bash
  bash -c "$(wget -qO - https://github.com/tteck/Proxmox/raw/main/ct/alpine-zigbee2mqtt.sh)"
  ~~~

  <h3 align="center" id="heading"> Default Settings:  256MiB RAM - 300MiB Storage - 1vCPU </h3>

   **Determine the location of your adapter**
    
  ~~~bash
  ls -l /dev/serial/by-id
  ~~~
  Example Output: <code class="highlighter-rouge">lrwxrwxrwx 1 root root 13 Jun 19 17:30 usb-1a86_USB_Serial-if00-port0 -> ../../ttyUSB0</code>
  
  
    **Before you start Zigbee2MQTT you need to edit the [configuration.yaml](https://www.zigbee2mqtt.io/guide/configuration/)**

  Debian<br>
  ~~~bash
  nano /opt/zigbee2mqtt/data/configuration.yaml
  ~~~
  Alpine<br>
  ~~~bash
  nano /etc/zigbee2mqtt/configuration.yaml
  ~~~

  Save and exit the editor with Ctrl+O, Enter and Ctrl+X
  
  Example:
  ~~~yaml
  frontend:
    port: 9442
  homeassistant: true
  permit_join: false
  mqtt:
    base_topic: zigbee2mqtt
    server: 'mqtt://192.168.86.224:1883'
    user: usr
    password: pwd
    keepalive: 60
    reject_unauthorized: true
    version: 4
  serial:
    port: /dev/serial/by-id/usb-1a86_USB_Serial-if00-port0
    #adapter: deconz		#(uncomment for ConBee II)
  advanced:
    pan_id: GENERATE
    network_key: GENERATE
    channel: 20
  ~~~
   **Zigbee2MQTT can be started after completing the configuration**

  Debian<br>  
  ~~~bash
  cd /opt/zigbee2mqtt && npm start
  ~~~
  Alpine<br>  
  ~~~bash
  rc-update add zigbee2mqtt default
  ~~~`

